import './autopilot-caller.css'
import Checkbox from '@mui/material/Checkbox';
import SyncIcon from '@mui/icons-material/Sync';
import SyncDisabledIcon from '@mui/icons-material/SyncDisabled';
import { Tooltip } from '@mui/material';
import Button from '@mui/material/Button';
import HeadsetMicIcon from '@mui/icons-material/HeadsetMic';
import { useState } from 'react';
import axios from 'axios'

const getEndzonePrediction = async (url, modelID, predictionParams) => {
    // Axios Async call to service
    let data = {
        modelID: modelID,
        predictionParams: predictionParams
    }
    return axios.post(url, data).then((response) => {
        return response.data;
    }, (error) => {
        console.log(error);
        return 'A Unexpected Error has Occurred';
    })
}
  

export default function AutopilotCaller(props){
    const [isAutoPredictEnabled, updateIsAutoPredictEnabled] = useState(false);
    const [autopilotOutputText, setAutopilotOutputText] = useState("Inside Run")
    // Create Model on the fly

    const handleGetPredictionClick = async () => {
        setAutopilotOutputText(await getEndzonePrediction(props.url, props.modelID, props.predictionParams));
    }

    return (
        <div className="autopilot-caller-div">
            <div className='header-div'>
                <h2>Coach Assistant</h2>
                <div className='header-toolbar-div'>
                    {/* Predict */}
                    <Button onClick={() => handleGetPredictionClick()} variant="contained" startIcon={<HeadsetMicIcon />} sx={{backgroundColor: '#17181d', fontFamily: 'Audiowide'}}>
                        Predict Play
                    </Button>
                    {/* Auto Refresh */}
                    <Tooltip title='Enable Automatic Prediction' arrow={true}>
                        <Checkbox 
                            onChange={(e) => updateIsAutoPredictEnabled(e.target.checked)} 
                            icon={<SyncIcon sx={{fontSize: '20px', color: 'white'}} />} 
                            checkedIcon={<SyncDisabledIcon sx={{fontSize: '20px', color: 'white'}}/>} 
                            sx={{backgroundColor: isAutoPredictEnabled ? '#67696e' : '#17181d', marginLeft: '5px'}}
                        />
                    </Tooltip>
                </div>
            </div>
            <div className='autopilot-output'>
                <p>{autopilotOutputText}</p>
            </div>
        </div>
    )
}