import React, { Component } from 'react';
import { Chart, LineController, Tooltip, LineElement, PointElement, LinearScale, Title,CategoryScale, BarElement, Legend } from 'chart.js';
import { Bar } from 'react-chartjs-2';
import EndzoneSelect from '../../selectors/endzone-select/endzone-select';
import './bar-chart.css'

import {endzone_colum_select_options, format_barChart_data} from '../utils'

Chart.register(LineController, LineElement, PointElement, LinearScale, Title, CategoryScale, BarElement, Tooltip, Legend);
Chart.defaults.color = '#ffffff';
Chart.defaults.font = {size: 15}
class BarChartWrapper extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value_col: null,
      group_by_col: null,
      chartData: format_barChart_data(props.data, "O_Formation", "Play_Type")
    };
  }

  componentDidUpdate(prevProps) {
    if (this.props.data !== prevProps.data) {
      this.reload_data()
    }
}

  reload_data = () => {
    let chart_data = format_barChart_data(this.props.data, this.state.group_by_col ? this.state.group_by_col : 'O_Formation', this.state.value_col ?  this.state.value_col : 'Play_Type')
    this.setState({chartData: chart_data});
  }

  options = {
    scales: {
      y: {
        beginAtZero: true,
        family: "Audiowide"
      },
    },
    plugins: {
      tooltip: {
        enabled: true,
      },
      legend: {
        display: true, // Display the legend
        position: 'top', // Set the position of the legend (top, bottom, left, right)
        labels: {
          font: {
            family: 'Audiowide', // Change the font family
            size: 16, // Change the font size
            weight: '100', // Change the font weight
            color: 'white'
          },
        }
      },

    },
    responsive: true,
    interaction: {
      mode: 'index',
      intersect: false,
    }
  };

  render() {
    return  (
        <div className='bar-graph-div'>
            <div className='bar-chart-controls'>
                <div className='select-div'>
                    <div className='select'>
                        <EndzoneSelect handleChange = {(change) => this.setState({group_by_col: change[0].value}, () => this.reload_data())} placeholder = "Group By..." data={this.props.availableOptions ? this.props.availableOptions : endzone_colum_select_options} ></EndzoneSelect>
                    </div>
                    <div className='select'>
                        <EndzoneSelect handleChange = {(change) => this.setState({value_col: change[0].value}, () => this.reload_data())} placeholder = "Value..." data={this.props.availableOptions ? this.props.availableOptions : endzone_colum_select_options} ></EndzoneSelect>
                    </div>
                </div>  
            </div>
            <div className='bar-chart'>
                <Bar data={this.state.chartData} options={this.options} />
            </div>
        </div>

    )
    
  }
}

export default BarChartWrapper;