import React from 'react'
import { useState } from 'react';
import './radar-chart.css'
import { Radar } from 'react-chartjs-2';
import { Chart, RadialLinearScale, Filler } from 'chart.js';
import { get_color_by_index } from '../utils';
import EndzoneSelect from '../../selectors/endzone-select/endzone-select';

Chart.register(RadialLinearScale, Filler);
Chart.defaults.color = '#ffffff';
Chart.defaults.font = {size: 15}

const endzone_colum_select_options = [
    {"value": "Drive", "label": "Drive"},
    {"value": "Down", "label": "Down"},
    {"value": "Coverage", "label": "Coverage"},
    {"value": "D_Formation", "label": "Defensive Formation"},
    {"value": "Formation_Strength", "label": "Formation Strength"},
    {"value": "Hash", "label": "Hash"},
    {"value": "O_Formation", "label": "Offensive Formation"},
    {"value": "Motion", "label": "Offensive Motion"},
    {"value": "Pass_Zone", "label": "Pass Zone"},
    {"value": "Play_Type", "label": "Play Type"},
    {"value": "Play_Type_Dir", "label": "Play Type Dir"},
    {"value": "Possession", "label": "Possession"},
    {"value": "Pressure_Left", "label": "Pressure Left"},
    {"value": "Pressure_Middle", "label": "Pressure Middle"},
    {"value": "Pressure_Right", "label": "Pressure Right"},
    {"value": "Quarter", "label": "Quarter"}
];

const formatChartData = (data, inGroupByCol, inCountByCol) => {

    const uniqueFormations = [...new Set(data.map((d) => d[inGroupByCol]))];
    const uniqueValues = [...new Set(data.map((d) => d[inCountByCol]))];
    const colorMap = {};
    uniqueValues.forEach((value, index) => {
      const color = get_color_by_index(index) + '66';
      colorMap[value] = color;
    });
    // Iterate through unique plays
    const datasets = uniqueValues.map((value) => {
        // Get Color from Color Map
        const color = colorMap[value];
        // Go through unique formations
        const dataByPlayType = uniqueFormations.map((formation) => {
            const matchingData = data.filter((d) => d[inCountByCol] === value && d[inGroupByCol] === formation);
            return matchingData ? matchingData.length : 0;
        });
  
      return {
        label: value,
        data: dataByPlayType,
        backgroundColor: color,
        borderColor: color,
        pointBackgroundColor: color,
        pointHoverBackgroundColor: color,
        pointHoverBorderColor: color,
        fill: true, // Set fill property to true
        borderWidth: 2,
      };
    });
  
    return {
      labels: uniqueFormations,
      datasets: datasets,
    };
}

export default function RadarChartWrapper ({ data, options }){
    const [groupByCol, setGroupByCol] = useState('O_Formation')
    const [countByCol, setCountByCol] = useState('Coverage')

    const chartData = formatChartData(data, groupByCol, countByCol);

    const chartOptions = {
        scales: {
            r: {
                ticks: {
                    color: 'black',
                    
                },
                backgroundColor: 'grey'
            }
        },
        scale: {
        type: 'radialLinear',
        ticks: {
            beginAtZero: true,
        },
        pointLabels: {
            font: {
            family: 'Audiowide',   
            },
            fontColor: 'black'
        },
        },
        plugins: {
            tooltip: {
            enabled: true,
            },
            legend: {
            display: true, // Display the legend
            position: 'top', // Set the position of the legend (top, bottom, left, right)
            labels: {
                font: {
                family: 'Audiowide', // Change the font family
                size: 16, // Change the font size
                weight: '100', // Change the font weight
                color: 'white'
                },
            }
            },
    
        },
        elements: {
            area: {
            fill: true, // Enable filling between points
            },
        },
    };

    return (
        <div style={{paddingTop: '10px'}}>
            <div className='select-div'>
                <div className='select'>
                    <EndzoneSelect handleChange = {(change) => setGroupByCol(change[0].value)} placeholder = "Group By..." data={options ? options : endzone_colum_select_options} ></EndzoneSelect>
                </div>
                <div className='select'>
                    <EndzoneSelect handleChange = {(change) => setCountByCol(change[0].value)} placeholder = "Count..." data={options ? options : endzone_colum_select_options} ></EndzoneSelect>
                </div>
            </div>  
        <div className='radar-chart-div'>
            <Radar data={chartData} options={chartOptions} />
        </div>

        </div>

    );
};

