import React from 'react';
import {Chart, ArcElement} from 'chart.js'
import { Doughnut } from 'react-chartjs-2';
import EndzoneSelect from '../../selectors/endzone-select/endzone-select';
import './donut-chart.css'

import { IconButton, Tooltip } from '@mui/material';
import VisibilityIcon from '@mui/icons-material/Visibility';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';

import { endzone_pie_chart_select_options } from '../utils';
import { format_pie_chart_data } from '../utils';

Chart.register(ArcElement);
class PieWrapper extends React.Component {
    constructor (props) {
        super(props);
        this.state = {
            column: null,
            chartTitle: this.props.defaultLabel ? this.props.defaultLabel : '',
            chart_data: format_pie_chart_data(this.props.data, this.props.defaultColumn ? this.props.defaultColumn : 'Play_Type'),
            show_legend: false
        }
    }

    componentDidUpdate(prevProps) {
        if (this.props.data !== prevProps.data) {
          this.reload_data()
        }
    }

    showLegend = () => {this.setState({show_legend: !this.state.show_legend})}

    reload_data = () => {
        let chart_data = format_pie_chart_data(this.props.data, this.state.column ?  this.state.column : this.props.defaultColumn)
        this.setState({chart_data: chart_data});
      }
  

    render() {
        let options = {
            plugins: {
              tooltip: {
                enabled: true,
              },
              legend: {display: this.state.show_legend},
              title: {
                    display: true,
                    text: this.state.chartTitle,
                    font: {
                    family: 'Audiowide',
                    size: 18,
                    weight: '100',
                    color: 'white'
                    }
                }
            },
            responsive: true,
            interaction: {
              mode: 'index',
              intersect: false,
            },
            centerText: {
                display: true,
                text: `90%`
            }
          };
        return (
            <div className='box-chart-section'>

                <div className='pie-chart-container'>
                    <div className='pie-chart'>
                        <Doughnut data={this.state.chart_data} options={options}/>
                    </div>
                    <div className='select-div'>
                        <EndzoneSelect handleChange = {(change) => this.setState({column: change[0].value, chartTitle: change[0].label}, () => this.reload_data())} data={endzone_pie_chart_select_options} ></EndzoneSelect>
                        <IconButton onClick={this.showLegend} sx={{width: '1%'}}>
                            <Tooltip sx={{fontFamily: "Audiowide"}} title={"Show Legend"}>
                                {this.state.show_legend ? <VisibilityIcon style={{color: 'white', height: '2.5vh', minHeight: '15px'}}></VisibilityIcon> : <VisibilityOffIcon style={{color: 'white', height: '2.5vh', minHeight: '15px'}}></VisibilityOffIcon>}
                            </Tooltip>
                        </IconButton>
                    </div>
                </div>
                <div>

                </div>
            </div>
        );
    }

}

export default PieWrapper;