export const get_chart_colors = (number_of_colors) => {return ["#0984e3", "#00dc91", "#de425b", "#ffed8f", "#00a9f6", "#09e367", "#00b9ed"].slice(0, number_of_colors)}

export const get_color_by_index = (index) => {return ["#0984e3", "#00dc91", "#de425b", "#ffed8f", "#00a9f6", "#09e367", "#00b9ed"][index]}

export const format_pie_chart_data = (data, column) => {
    let unique_values = get_unique_values(data, column)
    let values = Array(unique_values.length).fill(0)
    for(let index in data){values[unique_values.indexOf(data[index][column])]++}
    return {
        labels: unique_values,
        datasets: [{
            data: values,
            backgroundColor: get_chart_colors(unique_values.length)
        }]
    };
}

export const format_barChart_data = (data, group_by_col, value_col) => {
    let uniques = get_unique_groups_and_values(data, group_by_col, value_col);
    let unique_groups = uniques[0];
    let unique_values = uniques[1];

    // Create Data Structures
    let base_values = Array(unique_groups.length).fill(0)
    let dataset_values = Array(unique_values.length).fill(base_values).map(() => [...base_values]);

    for(let index in data){dataset_values[unique_values.indexOf(data[index][value_col])][unique_groups.indexOf(data[index][group_by_col])]++} 
    return {
        labels: unique_groups,
        datasets: dataset_values.map((array, index) => {
            return {
                label: unique_values[index],
                data: array,
                backgroundColor: get_color_by_index(index)
            }
        })
    };
}


const get_unique_values = (data, column) => {
    let unique_values = []
    for(let index in data){
        let value_item = data[index][column]
        if(!unique_values.includes(value_item)){unique_values.push(value_item)}
    }
    return unique_values
} 


const get_unique_groups_and_values = (data, group_by_col, value_col) => {
    let unique_groups = []
    let unique_values = []
    for(let index in data){
        let group_by_item = data[index][group_by_col];
        let value_item = data[index][value_col]
        // Unique Groups
        if(!unique_groups.includes(group_by_item)){unique_groups.push(group_by_item)}
        //Unique Values
        if(!unique_values.includes(value_item)){unique_values.push(value_item)}
    }
    return [unique_groups, unique_values]
}






