import GaugeChart from 'react-gauge-chart'
import { Tooltip } from '@mui/material'
import './gauge-chart.css'

export default function EndzoneGaugeChart(props){
    return (
        <Tooltip title={props.definition} arrow={true}>
            <div className='gauge-chart-div'>
                    <GaugeChart
                        nrOfLevels={20} 
                        colors={["#0984e3", "#008c40"]} 
                        arcWidth={0.15} 
                        percent={props.percentage} 
                    />
                    <label>{props.label}</label>
            </div>
        </Tooltip>
    )
}