import React, { Component } from "react";
import ReactSelect, { components } from "react-select";
import "./endzone-select.css";

const Option = (props) => {
  return (
    <div>
      <components.Option {...props}>
        <input type="checkbox" checked={props.isSelected} onChange={() => null} />{" "}
        <label>{props.label}</label>
      </components.Option>
    </div>
  );
};

export default class EndzoneSelect extends Component {
  constructor(props) {
    super(props);
    this.state = {
      optionSelected: [],
      menuIsOpen: false
    };
  }

  handleChange = (selected) => {
    this.setState({
      optionSelected: selected,
      menuIsOpen: false
    });
    this.props.handleChange([selected]);
  };

  handleMenuOpen = () => {
    this.setState({
      menuIsOpen: true
    });
  };

  render() {
    const sortedOptions = this.props.data.sort((a, b) =>
      a.label.localeCompare(b.label)
    );

    return (
      <div className="endzone-multiselect-div" onBlur={() => this.setState({menuIsOpen: false})}>
          <ReactSelect
            options={sortedOptions}
            isMulti={false}
            placeholder={this.props.placeholder ? this.props.placeholder : 'Select...'}
            closeMenuOnSelect={false}
            hideSelectedOptions={true}
            components={{ Option }}
            onChange={this.handleChange}
            allowSelectAll={true}
            value={this.state.optionSelected}
            id={this.props.id}
            menuIsOpen={this.state.menuIsOpen}
            onMenuOpen={this.handleMenuOpen}
          />
      </div>
    );
  }
}
